@import "sass/helpers/variables";

// Start Header
.helpdesk-header {
  top: 0;
  z-index: 5;
  .login {
    @media #{$maxTab} {
      flex-direction: column;
      align-items: stretch;
      justify-content: center;
      text-align: center;
      padding-top: 20px;
    }
    @media #{$tablet} {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding: 20px 0 10px;
    }
    .login-item {
      @media #{$maxTab} {
        width: 100%;
        &:last-child {
          border-top: 0 !important;
          padding-top: 0 !important;
        }
      }
      @media #{$tablet} {
        width: 48%;
        margin-top: 0 !important;
      }
    }
    .submit-link {
      font-size: 16px;
      line-height: 20px;
      color: #5b5863;
      transition: all 0.3s ease-in-out;
      margin-right: 40px;
      @media #{$maxTab} {
        align-items: stretch;
        border: 1px solid #fd3e72;
        border-radius: 3px;
        display: block;
        padding: 14px;
        color: $primaryColor;
      }
      @media #{$tablet} {
        margin-right: 0;
      }
      
      &:hover {
        color: $primaryColor;
      }
    }
    .signin-btn {
      padding: 8px 32px;
      font-size: 16px;
      @media #{$maxTab} {
        display: block;
        min-height: 50px;
        line-height: 50px;
        padding: 0;
      }
      @media #{$tablet} {
        margin-top: 0;
      }
    }
  }
}

// Start Main Banner
.main-banner {
  background: url(../imgs/helpdesk/main-banner.svg);
  background-size: cover;
  margin-top: 67px;
  background-position: center;
  @media #{$mobile} {
    background: linear-gradient(112.99deg, #fe6556 -8.34%, #fd3e72 60.54%, #49a8ff 185.26%);
  }
  h1 {
    color: #fff;
    font-size: 47px;
    line-height: 140%;
    padding: 74px 0 110px;
    @media #{$tablet} {
      font-size: 42px;
      padding: 55px 0 90px;
    }
    @media #{$mobile} {
      font-size: 28px;
      padding: 48px 0 78px;
    }
  }
  form {
    background: #ffffff;
    border: 1px solid #ebe8df;
    box-shadow: 0px 6px 9px rgba(230, 228, 223, 0.4), 0px 30px 29px rgba(240, 238, 233, 0.3);
    border-radius: 10px;
    position: absolute;
    bottom: -44px;
    left: 15px;
    right: 15px;
    transition: all 0.3s ease-in-out;
    @media #{$tablet} {
      bottom: -36px;
    }
    @media #{$mobile} {
      bottom: -25px;
    }
    input {
      width: 100%;
      display: block;
      padding: 29px 40px;
      border: 0;
      font-size: 20px;
      background-color: transparent;
      @media #{$tablet} {
        padding: 20px 30px;
      }
      @media #{$mobile} {
        height: 50px;
        font-size: 14px;
        line-height: 20px;
        padding: 10px 16px;
      }
      &::placeholder {
        color: #8d8b92;
        opacity: 0.5;
      }
    }
    button {
      background-color: #fff;
      position: absolute;
      top: 0;
      right: 0;
      border: 0;
      cursor: pointer;
      width: 88px;
      height: 100%;
      border-radius: 0 10px 10px 0;
      @media #{$mobile} {
        width: 56px;
        svg {
          width: 20px;
          height: 20px;
        }
      }
      svg.close-icon {
        display: none;
      }
    }
  }
}

// Start Helpdesk Features
.helpdesk-features {
  padding: 100px 0 60px;
  @media #{$mobile} {
    padding: 60px 0 30px;
  }
  .row {
    > div {
      display: flex;
    }
  }
  .feat {
    padding: 30px 40px;
    background: #ffffff;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    margin-bottom: 30px;
    transition: all 0.3s ease-in-out;
    width: 100%;
    &:hover {
      box-shadow: 0px 11px 25px rgba(0, 0, 0, 0.15);
    }
    @media (max-width: 1200px) {
      padding: 30px;
    }
    @media #{$mobile} {
      padding: 30px 16px;
    }
    .feat-content {
      @media #{$maxTab} {
        flex-direction: column;
        text-align: center;
      }
    }
    .icon {
      margin-right: 50px;
      @media #{$betweentabdesk} {
        margin-right: 30px;
      }
      @media #{$maxTab} {
        margin-right: 0;
        margin-bottom: 30px;
        img {
          height: 114px;
        }
      }
    }
    h3 {
      font-size: 24px;
      line-height: 30px;
      margin-bottom: 10px;
      @media #{$mobile} {
        font-size: 18px;
        line-height: 22px;
      }
    }
    p {
      font-size: 18px;
      line-height: 160%;
      @media #{$tablet} {
        font-size: 16px;
      }
      @media #{$mobile} {
        font-size: 14px;
        line-height: 160%;
      }
    }
  }
}

// Start Promoted Articles
.promoted-articles {
  padding: 55px 0 80px;
  @media #{$maxTab} {
    padding: 60px 0;
    .promoted-articles-image {
      margin-bottom: 30px;
    }
  }
  .title {
    font-size: 38px;
    line-height: 160%;
    margin-bottom: 20px;
    @media #{$mobile} {
      font-size: 24px;
      line-height: 160%;
    }
  }
  .accordion {
    .question {
      .question-header {
        h2 {
          font-size: 20px;
          line-height: 140%;
          position: relative;
          padding: 15px 15px 15px 0;
          cursor: pointer;
          border-bottom: 1px solid #f4f4f4;
          transition: all 0.3s ease-in-out;
          span {
            color: $primaryColor;
          }
          @media #{$maxTab} {
            font-size: 18px;
          }
          &.collapsed {
            @media (min-width: 1199px) {
              &:hover {
                span {
                  color: $primaryColor;
                  opacity: 1;
                }
                &:after,
                &:before {
                  background: $primaryColor;
                }
              }
            }
            span {
              transition: 0.3s;
              color: #5b5863;
              opacity: 0.7;
            }
          }
          &:before,
          &:after {
            background: $primaryColor;
            content: "";
            height: 2px;
            right: 0;
            position: absolute;
            top: 50%;
            width: 16px;
            border-radius: 40px;
            transition: transform 500ms ease;
            @media #{$mobile} {
              width: 12px;
            }
          }
          &:after {
            transform-origin: center;
          }
          &.collapsed {
            &:after,
            &:before {
              background: #878489;
            }
            &:after {
              transform: rotate(90deg);
            }
            &:before {
              transform: rotate(180deg);
            }
          }
        }
      }
      .question-body {
        padding: 15px 0 30px;
        padding-right: 50px;
        border-bottom: 1px solid #f4f4f4;
        p {
          font-size: 18px;
          line-height: 140%;
          opacity: 0.7;
          margin-bottom: 15px;
          @media #{$mobile} {
            font-size: 14px;
            line-height: 160%;
          }
        }
        @media #{$mobile} {
          font-size: 14px;
          line-height: 24px;
          padding-right: 20px;
        }
        .read-more {
          padding: 12px 35px;
          border: 1px solid $primaryColor;
          @media #{$mobile} {
            width: 100%;
            text-align: center;
          }
          svg {
            margin-right: 5px;
          }
        }
      }
    }
  }
  .text {
    margin: 55px 0 20px;
    font-size: 18px;
    line-height: 140%;
    @media #{$mobile} {
      text-align: center;
    }
  }
  a.blog-link {
    padding: 17px 34px;
    @media #{$mobile} {
      width: 100%;
    }
  }
}

.helpdesk-articles {
  padding: 100px 0 90px;
  @media #{$mobile} {
    padding: 55px 0;
  }

  .children-flex {
    > div {
      display: flex;
    }
  }
  #accordion {
    width: 100%;
    position: sticky;
    top: 100px;
    @media #{$mobile} {
      display: none;
    }
  }
  .parent-link,
  .child-link {
    border-bottom: 1px solid #f3ecec;
    .card-header {
      cursor: pointer;
      border: 0;
      background: none;
      svg {
        transition: all 0.3s ease-in-out;
        transform: rotate(180deg);
      }
      &.collapsed {
        svg {
          transform: rotate(0);
        }
      }
    }
    .card-body {
      padding: 8px 0 10px 25px;
      border-top: 1px solid #f3ecec;
      li {
        a {
          padding: 8px 0;
          opacity: 0.8;
          display: inline-block;
          transition: all 0.3s ease-in-out;
          &.active {
            color: $primaryColor;
            opacity: 1;
          }
          &:hover {
            opacity: 1;
            color: #5b5863;
          }
        }
      }
    }
  }
  .parent-link {
    .card-header {
      padding: 15px 0 15px 10px;
      @media #{$tablet} {
        font-size: 16px;
      }
    }
  }
  .child-link {
    .card-header {
      padding: 15px 0 15px 0;
      opacity: 0.8;
    }
    .card-body {
      li {
        a {
          opacity: 0.6;
        }
      }
    }
  }
  .articles-content {
    padding-left: 30px;
    border-left: 1px solid #f3ecec;
    @media #{$mobile} {
      padding-left: 0;
      border-left: 0;
    }
    .breadcrumb {
      background: none;
      padding-left: 0;
      @media #{$mobile} {
        justify-content: center;
      }
    }
    .breadcrumb-item {
      font-size: 18px;
      line-height: 160%;
      @media #{$tablet} {
        font-size: 16px;
      }
      @media #{$mobile} {
        font-size: 14px;
      }
      &.active {
        color: $primaryColor;
        opacity: 0.6;
      }
      a {
        color: $primaryColor;
        opacity: 0.6;
        transition: all 0.3s ease-in-out;
        &:hover {
          opacity: 1;
        }
      }
    }
    .breadcrumb-item + .breadcrumb-item::before {
      color: $primaryColor;
      opacity: 0.6;
      content: "|";
    }
    .articles {
      h2 {
        margin-bottom: 30px;
        @media #{$maxTab} {
          margin-bottom: 20px;
          font-size: 30px;
        }
        @media #{$mobile} {
          font-size: 24px;
          text-align: center;
        }
      }
      .articles-list {
        padding-left: 25px;
        li {
          @media #{$tablet} {
            font-size: 16px;
          }
          @media #{$mobile} {
            font-size: 14px;
          }
          &:not(:last-child) {
            margin-bottom: 20px;
          }
          p {
            @media #{$tablet} {
              font-size: 16px;
            }
            @media #{$mobile} {
              font-size: 14px;
            }
          }
        }
      }
    }
    .link {
      transition: all 0.3s ease-in-out;
      &:hover {
        color: $primaryColor;
      }
    }
    .article-share {
      display: flex;
      margin-top: 60px;
      @media #{$maxTab} {
        justify-content: center;
        margin-top: 40px;
      }
      li {
        display: flex;
        align-items: center;
        &:not(:last-child) {
          margin-right: 30px;
        }
        svg {
          margin-right: 10px;
        }
      }
      p {
        font-weight: 500;
        @media #{$maxTab} {
          display: none;
        }
      }
    }
    .article-evaluation {
      background: linear-gradient(106.44deg, #fe6556 -9.53%, #fd3e72 54.31%, #49a8ff 158.1%);
      border-radius: 10px;
      padding: 30px 0;
      color: #fff;
      text-align: center;
      @media #{$mobile} {
        padding: 30px 20px;
      }
      .btns {
        margin: 25px 0 12px;
        button {
          width: 176px;
          height: 52px;
          line-height: 52px;
          border: 0;
          font-weight: bold;
          font-size: 20px;
          border-radius: 5px;
          @media #{$tablet} {
            width: 150px;
            height: 48px;
            line-height: 48px;
            font-size: 18px;
          }
          @media #{$mobile} {
            display: block;
            width: 100%;
          }
          &.yes {
            margin-right: 20px;
            @media #{$mobile} {
              margin-right: 0;
              margin-bottom: 15px;
            }
          }
          &.no {
            color: #fff;
            border: 1px solid #ffffff;
            background: none;
          }
        }
      }
      p {
        @media #{$mobile} {
          font-size: 14px;
          line-height: 160%;
        }
        &.helpful-count {
          @media #{$mobile} {
            font-size: 12px;
            line-height: 160%;
          }
        }
        a {
          color: #fff;
          text-decoration: underline !important;
        }
      }
    }
  }
}

.breadcrumb {
  background: none;
  padding-left: 0;
  @media #{$mobile} {
    justify-content: center;
  }
}
.breadcrumb-item {
  font-size: 18px;
  line-height: 160%;
  @media #{$tablet} {
    font-size: 16px;
  }
  @media #{$mobile} {
    font-size: 14px;
  }
  &.active {
    color: $primaryColor;
    opacity: 0.6;
  }
  a {
    color: $primaryColor;
    opacity: 0.6;
    transition: all 0.3s ease-in-out;
    &:hover {
      opacity: 1;
    }
  }
}
.breadcrumb-item + .breadcrumb-item::before {
  color: $primaryColor;
  opacity: 0.6;
  content: "|";
}

.search-results {
  padding: 95px 0;
  @media #{$maxTab} {
    padding: 55px 0;
  }
  .search-results-title {
    margin-bottom: 50px;
    @media #{$tablet} {
      font-size: 30px;
      margin-bottom: 30px;
    }
    @media #{$mobile} {
      margin-bottom: 30px;
      font-size: 24px;
      line-height: 160%;
      text-align: center;
    }
  }
  .search-box {
    background: #ffffff;
    box-shadow: 0px 8px 27px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 20px 30px;
    position: relative;
    @media #{$mobile} {
      padding: 20px 16px;
    }
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    h3 {
      @media #{$mobile} {
        font-size: 18px;
        line-height: 160%;
      }
    }
    .breadcrumb {
      @media #{$mobile} {
        justify-content: flex-start;
        margin-bottom: 0;
      }
    }
    .breadcrumb-item a {
      font-size: 14px;
      line-height: 160%;
      color: #5b5863;
      @media #{$mobile} {
        font-size: 12px;
      }
    }
    .breadcrumb-item + .breadcrumb-item::before {
      color: #5b5863;
    }
    .search-result-desc {
      .text {
        max-width: 750px;
        @media #{$betweentabdesk} {
          max-width: 550px;
          font-size: 16px;
        }
        @media #{$mobile} {
          font-size: 14px;
        }
      }
      .search-result-data {
        text-align: right;
        position: absolute;
        right: 30px;
        bottom: 20px;
        @media #{$tablet} {
          position: static;
          display: flex;
          align-items: center;
          margin-top: 20px;
        }
        @media #{$mobile} {
          position: static;
          text-align: left;
          margin-top: 30px;
        }
      }
      .vote {
        margin-bottom: 15px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        @media #{$tablet} {
          margin-right: 30px;
          margin-bottom: 0;
          justify-content: flex-start;
        }
        @media #{$mobile} {
          justify-content: flex-start;
          margin-bottom: 10px;
        }
        .like {
          margin-right: 10px;
        }
      }
      .search-meta-data {
        @media #{$mobile} {
          font-size: 12px;
        }
        .divider {
          margin: 0 10px;
        }
      }
    }
  }
  .search-pagination {
    margin-top: 70px;
    @media #{$mobile} {
      margin-top: 40px;
    }
    .prev {
      float: left;
    }
    .next {
      float: right;
    }
    ul {
      text-align: center;
      li {
        cursor: pointer;
        width: 170px;
        line-height: 50px;
        height: 50px;
        font-weight: 500;
        font-size: 20px;
        text-align: center;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
        @media #{$tablet} {
          width: 140px;
          line-height: 44px;
          height: 44px;
          font-size: 18px;
        }
        @media #{$mobile} {
          width: 40px;
          height: 40px;
        }
        &.step {
          width: 44px;
          height: 44px;
          line-height: 44px;
          font-weight: 500;
          font-size: 20px;
          margin-top: 3px;
          transition: all 0.3s ease-in-out;
          @media #{$mobile} {
            width: 36px;
            height: 36px;
            line-height: 36px;
            margin-top: 2px;
          }
          &.current, &:hover {
            background-color: $primaryColor;
            color: #fff;
          }
        }
        &.prev {
          @media #{$mobile} {
            span {
              display: none;
            }
          }
          svg {
            margin-right: 10px;
            @media #{$mobile} {
              margin-right: 0;
            }
          }
          &.disabled {
            opacity: 0.3;
            border: 1px solid #C8C8C8;
            border-radius: 5px;
          }
        }
        &.next {
          color: $primaryColor;
          border: 1px solid $primaryColor;
          svg {
            margin-left: 10px;
            @media #{$mobile} {
              margin-left: 0;
            }
          }
          @media #{$mobile} {
            span {
              display: none;
            }
          }
        }
      }
    }
  }
}
